<template>
  <div
    :days="days"
    :dayIndex="dayIndex"
    :eventsArr="eventsArr"
    :month="month"
    :year="year"
    :currentDay="currentDay"
  >
    <div class="weekdays-wrapper">
      <div class="padding-block">
        <p class="day-date">{{ days[dayIndex].dayDate }}</p>
      </div>
    </div>
    <div class="hours-wrapper">
      <div class="hours-item">
        <div
          class="hours padding-block"
          :class="{ now: hour === isNowHour }"
          v-for="hour in hours"
          :key="hour"
          v-html="hour"
        ></div>
      </div>
      <div class="day-wrapper">
        <div
          class="day"
          :class="{
            current:
              currentDay === days[dayIndex].numberDay && month === currentMonth,
          }"
        >
          <TimeLine
            :style="{
              top:
                60 +
                (this.hours < 1
                  ? this.isNowMin
                  : this.hour * 60 + this.isNowMin) +
                'px',
            }"
          />
          <div
            class="day-hours hours padding-block"
            v-for="hour in hours"
            :key="hour"
            @click="$emit('showModal', days[dayIndex].numberDay, month, year)"
          >
            <span class="day-hours-fill">{{ hour }}</span>
            <div class="hours-content">
              <div class="event-list">
                <div v-for="event in eventsArr" :key="event.startTime">
                  <div
                    v-if="
                      days[dayIndex].clickDateCheck === event.day &&
                      compareStringHours(
                        hour,
                        event.startTime,
                        event.timeAsNumber
                      )
                    "
                    class="event"
                    :style="{ height: 59 * event.timeAsNumber + 'px' }"
                  >
                    <span>{{ event.startTime }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TimeLine from "../common/TimeLine.vue";
export default {
  name: "DayContent",
  components: {
    TimeLine,
  },
  mounted() {
    this.getHours();
  },
  props: {
    days: Array,
    dayIndex: Number,
    month: Number,
    year: Number,
    eventsArr: Array,
    currentDay: Number,
  },
  data() {
    return {
      hours: [
        "all&nbsp;day",
        "00:00",
        "01:00",
        "02:00",
        "03:00",
        "04:00",
        "05:00",
        "06:00",
        "07:00",
        "08:00",
        "09:00",
        "10:00",
        "11:00",
        "12:00",
        "13:00",
        "14:00",
        "15:00",
        "16:00",
        "17:00",
        "18:00",
        "19:00",
        "20:00",
        "21:00",
        "22:00",
        "23:00",
      ],
      currentMonth: new Date().getMonth(),
      isNowHour: "",
      hour: new Date().getHours(),
      isNowMin: 0,
    };
  },
  methods: {
    compareStringHours(hour, eventHour, height) {
      this.heightIndexOfEvent = height;
      return hour.slice(0, 2) === eventHour.slice(0, 2);
    },
    getHours() {
      const date = new Date();
      const hour =
        date.getHours().toString().length < 2
          ? "0" + date.getHours()
          : date.getHours();
      const min = date.getMinutes();
      this.isNowHour = `${hour}:00`;
      this.isNowMin = min;
    },
  },
};
</script>

<style lang="less" scoped>
.weekdays-wrapper {
  width: 100%;

  .weekdays {
    flex-grow: 1;
  }
}
.padding-block {
  display: flex;
  justify-content: center;
  background-color: #f5f6fa;
  color: #f5f6fa;
  font-size: 0.8125rem;
  font-weight: 400;
  text-align: center;
  padding: 21px;
}
.day-date {
  margin: 0;
  color: #a3a6b4;
  font-size: 0.75rem;
  font-weight: 700;
}
.day-wrapper {
  flex-grow: 1;
}
.day {
  position: relative;
  overflow-y: clip;
}
.hours-wrapper {
  display: flex;
}
.hours-item {
  border-right: 1px solid #eaf0f4;
}
.hours {
  background-color: inherit;
  color: #4d4f5c;

  &:not(:last-child) {
    border-bottom: 1px solid #eaf0f4;
  }
}
.day-hours {
  position: relative;

  &:last-child {
    border-right: none;
  }
  &-fill {
    visibility: hidden;
  }
  &:hover,
  &:focus {
    background-color: #fcfcfc;
  }
}
.hours-content {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  .event-list {
    .event {
      position: relative;
      z-index: 1;
      background-color: #3b86ff;
      color: #3b86ff;
      border-radius: 4px;
      padding: 7px 14px;
      text-align: left;

      span {
        font-weight: 700;
        font-size: 0.85rem;
        color: #ffffff;
      }
    }
  }
}
.current {
  background-color: #f5f6fa;
}
.now {
  font-weight: 700;
}
</style>
